    <p>4.2.1. Осуществлять поиск приобретателя на Объект любыми доступными Исполнителю способами.</p>
    <p>4.2.2. Организовывать показы Объекта для потенциальных приобретателей.</p>
    <p>4.2.3. Подобрать Альтернативу, используя по своему усмотрению любые доступные Исполнителю информационные ресурсы и организовать их просмотр.</p>
    <p>4.2.4. Организовать оформление договоров по отчуждению Объекта/приобретению Альтернативы в согласованном порядке при наличии согласия участников сделок, а также всех необходимых для этого документов.</p>
    <p>5. Расчеты и порядок оплаты</p>
    <p>5.1. Вознаграждение Исполнителя по договору составляет сумму в размере 110 000 (сто десять тысяч) рублей, НДС не облагается.</p>
    <p>5.2. Услуга по договору считается полностью и надлежащим образом оказанной Исполнителем и принятой Заказчиком в день государственной регистрации перехода права собственности на Объект и/или Альтернативу (в зависимости от согласованного порядка проведения сделок), а в случае нерегистрации по обстоятельствам, зависящим от Заказчика/собственника Объекта/приобретателя Альтернативы, — в день подписания договоров по отчуждению Объекта и приобретению Альтернативы.</p>
    <p>5.3. Расходы, связанные с совершением сделок:</p>
    <p>5.3.1. По отчуждению Объекта (аренда банковской ячейки, услуги нотариуса или составление договора в простой письменной форме, государственная регистрация перехода права собственности) оплачиваются приобретателем Объекта.</p>
    <p>5.3.2. По приобретению Альтернативы (аренда банковской ячейки, услуги нотариуса или составление договора в простой письменной форме, государственная регистрация перехода права собственности) оплачиваются приобретателем Объекта.</p>
    <p>6. Ответственность Сторон и основания освобождения от ответственности</p>
    <p>6.1. Стороны освобождаются от ответственности за неисполнение или ненадлежащее исполнение своих обязательств по настоящему договору, если причиной этому явились события непреодолимой силы, а для Исполнителя дополнительным основанием освобождения от ответственности являются также события, факторы или обстоятельства, не поддающиеся контролю и находящиеся вне власти Исполнителя (в частности, противоправные действия третьих лиц, решения, действия или бездействие третьих лиц, органов власти или управления, в том числе органа по регистрации прав на недвижимое имущество, издание актов, препятствующих надлежащему исполнению обязательств, изменение цен на рынке недвижимости, или т.п.).</p>
    <p>6.2. Исполнитель несет ответственность за неисполнение или ненадлежащее исполнение своих обязательств по договору в соответствии с действующим гражданским законодательством РФ при наличии вины.</p>
    <p>6.3. Исполнитель обеспечивает сохранность полученных им оригиналов правоустанавливающих документов на Объект.</p>
    <p>6.4. Исполнитель вправе потребовать от Заказчика уплаты всей предусмотренной договором суммы вознаграждения в случае отчуждения Заказчиком/собственником Объекта без участия Исполнителя в период действия настоящего договора или в течение 6 месяцев после его прекращения приобретателю, найденному Исполнителем, или его аффилированному лицу (под которым понимаются родственники любой степени родства, супруги и их родственники, юридические лица, в которых, приобретатель имеет акции (доли участия, паи и т.п.), а также иные лица, которые в соответствии с действующим законодательством или установившейся практикой признаются аффилированными).</p>
    <p>6.5. Исполнитель не несет ответственности за неисполнение или ненадлежащее исполнение обязательств по сделкам, заключенным их участниками, а также по другим вопросам, не входившим в обязанности Исполнителя по настоящему договору, включая вопросы взаимоотношений участников сделок с налоговыми органами.</p>
    <p>7. Основания и порядок прекращения договора</p>
    <p>7.1. Действие настоящего договора и предусмотренных в нем обязательств прекращается вследствие исполнения, досрочного расторжения настоящего договора по соглашению сторон, отказа Исполнителя или Заказчика от его исполнения по основаниям, указанным в законе или в настоящем договоре, или истечения срока действия, за исключением случая, предусмотренного пунктом 8.1.2 договора.</p>
    <p>В случае заключения договора Заказчиком в пользу третьего лица, изменение или прекращение договора производится без согласия последнего.</p>
    <p>7.2. Настоящий договор считается не исполненным по вине Заказчика, и Исполнитель вправе известить Заказчика о прекращении договора, в случае нарушения Заказчиком любого из предусмотренных договором обязательств, включая неявку Заказчика/ собственника Объекта/приобретателя Альтернативы на сделку, или иного препятствования совершению сделок со стороны Заказчика/собственника Объекта или приобретателя Альтернативы, и/или недостоверности информации об Объекте или гарантий, указанных в пунктах 1.5.1 и 3.3 настоящего договора. В этих случаях Исполнитель вправе потребовать от Заказчика уплаты вознаграждения по договору.</p>
    <p>8. Заключительные положения</p>
    <p>8.1. Договор вступает в силу с <inline-edit width="3em" v-model="document.item62"></inline-edit>.<inline-edit width="3em" v-model="document.item63"></inline-edit>.201<inline-edit width="1.5em" v-model="document.item64"></inline-edit> и действует до <inline-edit width="3em" v-model="document.item65"></inline-edit>.<inline-edit width="3em" v-model="document.item66"></inline-edit>.201<inline-edit width="1.5em" v-model="document.item67"></inline-edit> включительно. Если последний день срока действия договора приходится на субботу, воскресенье или праздничный день, днем окончания срока считается ближайший следующий за этим днем рабочий день.</p>
    <p>8.1.1. Действие договора может быть продлено путем подписания сторонами соответствующего соглашения или путем обмена сообщениями по электронной почте в следующем порядке: Исполнитель до окончания срока действия настоящего договора направляет Заказчику по адресу электронной почты (E-mail), указанному Заказчиком, предложение о продлении срока действия договора с указанием нового срока действия; при получении положительного ответа Заказчика настоящий договор считается продленным на новый срок, согласованный с Заказчиком.</p>
    <p>При множественности лиц на стороне Заказчика продление договора в вышеуказанном порядке одним из Заказчиков означает согласие остальных Заказчиков с продлением договора.</p>
    <p>8.1.2. Если в период действия настоящего договора участниками сделок подписаны договоры по отчуждению Объекта и/или по приобретению Альтернативы или совершены действия, направленные на обеспечение взаиморасчетов по сделкам, то в случае недостаточности оставшегося срока для завершения исполнения договора в объеме принятых Исполнителем обязательств, настоящий договор считается продленным на необходимый для его исполнения срок (в порядке ст. 158, 159 ГК РФ); в этом случае письменного оформления пролонгации договора не требуется.</p>
    <p>8.2. Заказчик извещается Исполнителем о дате, времени и месте оформления сделок не позднее, чем за два рабочих дня, способом по выбору Исполнителя (по электронной почте, факсу, СМС или др. или устно). При неполучении Заказчиком от Исполнителя извещения о дне сделок Заказчик обязан явиться и обеспечить явку всех собственников и пользователей Объекта, а также приобретателей Альтернативы на сделку в 12 часам 00 минутам последнего дня срока действия договора в офис по адресу: г. Москва, <inline-edit width="31em" v-model="document.item68"></inline-edit> <inline-edit width="41.5em" v-model="document.item69"></inline-edit>.</p>
    <p>8.3. В случае необходимости направления Заказчику письменного извещения Заказчик считается надлежащим образом извещенным, если сообщение было отправлено по адресу:<inline-edit width="38em" v-model="document.item1"></inline-edit> <inline-edit width="37em" v-model="document.item70"></inline-edit>, или по указанному Заказчиком адресу электронной почты.</p>
    <p>8.4. В случае возникновения спора стороны принимают меры к его разрешению путем переговоров, а при невозможности урегулирования спора указанным способом Заказчик обязан лично обратиться в независимую Примирительную комиссию. В дальнейшем споры, связанные с настоящим договором, разрешаются в суде по месту его заключения (г. Москва) согласно установленной действующим законодательством подведомственности.</p>
    <p>8.5. Заказчик в целях заключения, исполнения и архивации настоящего договора дает согласие Исполнителю на обработку (включая передачу через интернет) согласно ст. 3 Федерального Закона «О персональных данных» персональных данных (фамилии, имени, отчества; даты и места рождения; адреса проживания; реквизитов документа, удостоверяющего личность; семейного положения; контактных телефонов; адреса электронной почты и иных сведений) Заказчика и участников сделок (при этом Заказчик гарантирует, что персональные данные участников сделок и согласие на их передачу получены Заказчиком на законных основаниях). Согласие действительно в период действие настоящего договора и в течение пяти лет после его прекращения, и может быть отозвано в письменной форме.</p>
    <p>8.6. Заказчику разъяснены и понятны все положения договора, все пробелы в договоре и приложениях заполнены после предварительного согласия с Заказчиком и полностью одобрены им. Подписание настоящего договора Заказчиком свидетельствует об отсутствии возражений, замечаний и дополнений по его условиям.</p>
    <p>8.7. Заказчик согласен на бесплатное получение от Исполнителя аналитических обзоров рынка недвижимости, информации об акциях, скидках, новых услугах и предложениях, связанных с уставной деятельностью Исполнителя, любыми доступными способами связи (СМС, E-mail, и др.), в том числе после прекращения настоящего договора или отзыва согласия, указанного в п. 8.5.</p>
    <p>8.8. Договор составлен в двух экземплярах, имеющих равную юридическую силу.</p>
