    AsyncComponent: AsyncComponent(1)
  },
  computed: {
    document() {
      return this.$store.state.currentDocument;
    }
  },
  watch: {
    document: {
      handler: function(value) {
        this.$store.dispatch("updateCurrentDocument", {
          value
        });
      },
      deep: true
    }
  }
};
</script>


