import Vue from "vue";
import Vuex from "vuex";
import createPersistedState from "vuex-persistedstate";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    currentDocument: {
      item1: "123"
    }
  },
  mutations: {
    updateCurrentDocument(state, value) {
      state.currentDocument = value.value;
    }
  },
  actions: {
    updateCurrentDocument(context, value) {
      context.commit("updateCurrentDocument", value);
    }
  },
  plugins: [createPersistedState()]
});



